import React, { useState, useCallback } from 'react'
import { QuoteToken } from 'config/constants/types'
import { useApproveTreasury } from 'hooks/useApprove'
import useTokenBalance from 'hooks/useTokenBalance'
import styled from 'styled-components'
import { Button, Card, useModal } from 'uikit'
import DepositModal from 'views/Farms/components/DepositModal'
import { CardLogo } from 'views/Home/Home'

interface ExchangeCardProps {
  action: string
  fromToken: QuoteToken
  toToken: QuoteToken
  priceDesc: string
  onExchange: (amount: string) => void
  disabled?: boolean
  disabledDescription?: string
}

const ExchangeCard: React.FC<ExchangeCardProps> = ({
  action,
  fromToken,
  toToken,
  priceDesc,
  onExchange,
  disabled = false,
  disabledDescription,
}) => {
  const [requestedApproval, setRequestedApproval] = useState(false)
  const balance = useTokenBalance(fromToken)
  const { onApprove, isApproved } = useApproveTreasury(fromToken)

  const handleApprove = useCallback(async () => {
    try {
      setRequestedApproval(true)
      await onApprove()
      setRequestedApproval(false)
    } catch (e) {
      console.error(e)
    }
  }, [onApprove])

  const [onPresentDeposit] = useModal(<DepositModal max={balance} onConfirm={onExchange} tokenName={fromToken} />)

  return (
    <Card>
      <CardLogo token={toToken.toLocaleLowerCase()}>
        <StyledCardContentInner>
          <StyledCardTitle>{`${action} ${toToken}`}</StyledCardTitle>
          <StyledDesc>{priceDesc}</StyledDesc>
          <StyledCardActions>
            {!isApproved && !disabled ? (
              <Button disabled={requestedApproval} onClick={handleApprove}>
                {`Approve ${fromToken}`}
              </Button>
            ) : (
              <Button onClick={onPresentDeposit} disabled={disabled}>
                {disabledDescription || action}
              </Button>
            )}
          </StyledCardActions>
        </StyledCardContentInner>
      </CardLogo>
    </Card>
  )
}

const StyledCardTitle = styled.div`
  align-items: center;
  color: ${(props) => props.theme.colors.primary};
  display: flex;
  font-size: 20px;
  font-weight: 700;
  height: 64px;
  justify-content: center;
  margin-top: ${(props) => -props.theme.spacing[3]}px;
`

const StyledCardActions = styled.div`
  display: flex;
  justify-content: center;
  margin-top: ${(props) => props.theme.spacing[3]}px;
  width: 100%;
`

const StyledDesc = styled.span`
  color: ${(props) => props.theme.colors.tertiary};
`

const StyledCardContentInner = styled.div`
  align-items: center;
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: space-between;
`

export default ExchangeCard
