import React, { useCallback, useMemo } from 'react'
import styled from 'styled-components'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import Spacer from 'components/Spacer'
import { useBondStats, useCashPriceInEstimatedTWAP } from 'hooks/useExitBoardroom'
import { getDisplayBalance } from 'utils/formatBalance'
import { buyBonds, redeemBonds } from 'utils/callHelpers'
import UnlockButton from 'components/UnlockButton'
import { useTreasury } from 'hooks/useContract'
import useTokenBalance from 'hooks/useTokenBalance'
import { QuoteToken } from 'config/constants/types'
import ExchangeCard from './components/ExchangeCard'
import ExchangeStat from './components/ExchangeStat'

const BondToken: React.FC<any> = () => {
  const { account } = useWallet()
  const contract = useTreasury()
  const bondBalance = useTokenBalance(QuoteToken.SBO)
  const cashStat = useCashPriceInEstimatedTWAP()
  const [burnable, redeemable] = useBondStats()

  const handleBuyBonds = useCallback(
    async (amount: string) => {
      if (!amount) return

      await buyBonds(contract, amount, cashStat, account)
    },
    [cashStat, account, contract],
  )

  const handleRedeemBonds = useCallback(
    async (amount: string) => {
      if (!amount) return

      await redeemBonds(contract, amount, cashStat, account)
    },
    [cashStat, account, contract],
  )

  const isBondRedeemable = useMemo(() => Number(cashStat) > 1.0 && Number(redeemable) > 0, [
    cashStat,
    redeemable,
  ])
  const isBondPurchasable = useMemo(() => Number(cashStat) < 1.0, [cashStat])
  const description = useMemo(() => (isBondPurchasable ? 'Burnable Dollars' : 'Redeemable Bonds'), [isBondPurchasable])

  return account ? (
    <>
      <StyledBond>
        <StyledCardWrapper>
          <ExchangeCard
            action="Purchase"
            fromToken={QuoteToken.SDO}
            toToken={QuoteToken.SBO}
            priceDesc={
              !isBondPurchasable
                ? `${QuoteToken.SDO} is over peg`
                : `${Math.floor(100 / cashStat.toNumber() - 100)}% return when ${QuoteToken.SDO} is over`
            }
            onExchange={handleBuyBonds}
            disabled={!isBondPurchasable}
          />
        </StyledCardWrapper>
        <Spacer />
        <StyledCardWrapper>
          <ExchangeStat
            description={description}
            label={isBondPurchasable ? burnable?.toFixed(3) : redeemable?.toFixed(3)}
          />
          <Spacer />
          <ExchangeStat description="Last-Hour TWAP Price" label={`ðŸ’° ${QuoteToken.SDO} = $${cashStat?.toFixed(3) || '-'}`} />
        </StyledCardWrapper>
        <Spacer />
        <StyledCardWrapper>
          <ExchangeCard
            action="Redeem"
            fromToken={QuoteToken.SBO}
            toToken={QuoteToken.SDO}
            priceDesc={`${getDisplayBalance(bondBalance)} ${QuoteToken.SDO} Available`}
            onExchange={handleRedeemBonds}
            disabled={!isBondRedeemable}
            disabledDescription={!isBondRedeemable ? `Enabled when ${QuoteToken.SDO} over peg` : 'Redeem'}
          />
        </StyledCardWrapper>
      </StyledBond>
    </>
  ) : (
    <UnlockButton mt="8px" fullWidth />
  )
}

const StyledBond = styled.div`
  display: flex;
  width: 960px;
  @media (max-width: 768px) {
    width: 100%;
    flex-flow: column nowrap;
    align-items: center;
  }
`

const StyledCardWrapper = styled.div`
  display: flex;
  flex: 1;
  flex-direction: column;
  @media (max-width: 768px) {
    width: 80%;
  }
`

export default BondToken
