import React from 'react'
import styled from 'styled-components'
import Page from 'components/layout/Page'
import { Heading } from 'uikit'
import { Route, useRouteMatch } from 'react-router-dom'
import LaunchCountdown from 'components/LaunchCountdown'
import FlexLayout from 'components/layout/Flex'
import BondToken from './BondToken'

const Wrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 32px;
`

const launchTime = new Date(1621818673000)

const Bond: React.FC = () => {
  const { path } = useRouteMatch()

  const isLaunched = Date.now() >= launchTime.getTime()

  if (!isLaunched) {
    return (
      <Page>
      <FlexLayout>
        <Heading as="h1" size="lg" mb="50px" style={{ textAlign: 'center' }}>
          ðŸ’° Bonds
        </Heading>
      </FlexLayout>
        <Wrapper>
          <LaunchCountdown deadline={launchTime} />
        </Wrapper>
      </Page>
    )
  }

  return (
    <Page>
      <FlexLayout>
        <Heading as="h1" size="lg" mb="50px" style={{ textAlign: 'center' }}>
          ðŸ’° Bonds
        </Heading>
      </FlexLayout>
      <Route exact path={`${path}`}>
        <BondToken />
      </Route>
    </Page>
  )
}

export default Bond
